<template>
    <div class="logistics-info">
      <div class="head">
        <div class="progress">
          <div class="pro-content">
            <div :class="type==1?'active-content':''">已发货</div>
            <div :class="type==2?'active-content':''">运输中</div>
            <div :class="type==3?'active-content':''">已签收</div>
          </div>
          <div class="pro-con">
            <div class="point" :class="status>=0?'active-point':''"></div>
            <div class="line"></div>
            <div class="point" :class="status>=1?'active-point':''"></div>
            <div class="line"></div>
            <div class="point" :class="status>=3?'active-point':''"></div>
          </div>
           <div class="pro-content add-con">
            <div></div>
            <div>{{order.city}}</div>
          </div>
        </div>
        <div class="company-info" @click="toLogistics">
          <img src="@/assets/img/dingdanxiangqing--huoche.png" alt="">
          <div class="now-add-con">
            <p>配送企业：<span>{{order.logistics_name}}</span></p>
            <p>快递单号：<span>{{order.logistics_sn}}</span></p>
            <p>联系电话：<span>{{order.logistics_tel}}</span></p>
          </div>
        </div>
      </div>
      <div class="logistics-con">
        <div class="tit">物流信息</div>
        <div class="logistics-list">
          <div class="logistics-item" v-for="(item,index) in logisticsList" :key="item.is">
            <div class="dotted-line">
              <span :class="index===0?'iconfont icon-chenggong':'dotted'" ></span>
              <span class="item-line"></span>
            </div>
            <div class="info">
              <div>
                <span>{{item.time}}</span>
              </div>
              <div :class="index===0?'active-color':''">{{item.context}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
import { apiGet,apiPost } from '@/common/js/api';
export default {
    name: "logistics-info",
    
    data(){
      const query = this.$route.query;
      return{
        logisticsId:query.order_id,
        type:'',
        order:{},
        status:0,
        logisticsList:[
          {
            status:"已签收",
            date:"2018-08-24    14:00:00",
            detail:"[成都市]高新区天府二街蜀都中心22号"
          },
          {
            status:"派送中",
            date:"2018-08-24    14:00:00",
            detail:"[成都市]高新区市区分部土豆都正在为您第一次派件，联系电话：15110000000"
          },
          {
            status:"运输中",
            date:"2018-08-24    14:00:00",
            detail:"[广州市]广州市分发中心已揽件"
          },
          {
            status:"已发货",
            date:"2018-08-24    14:00:00",
            detail:"[广州市]卖家已发货"
          },
        ]
      }
    },
    components: { },
    computed: {},
    watch:{},
    beforeCreate(){

    },
    created(){
      this.accessToken=this.$cookie.get('accessToken')
      // this.accessToken='2eHG5laajTo6Z1kPthFiJFc4D_hHzj0R';
      this.getMallOrderDetail();
    },
    beforeMount(){

    },
    mounted(){

    },
    methods:{
      //获取订单详情
      getMallOrderDetail(){
        let formdata = new FormData();
        formdata.append('accessToken',this.accessToken);
        formdata.append('order_id',this.logisticsId)
        apiPost("/order/order-logistics",formdata).then(res=>{
          console.log(res)
          this.type=res.data.data.data.order.type;
          console.log(this.type);
          this.order=res.data.data.data.order;
          this.logisticsList = res.data.data.data.data;
          console.log(this.logisticsList)
        })
      },
      //前往物流信息
      toLogistics(){
        this.$router.push({
          path:"logisticsInfo",
          query:{
            logisticsId:12
          }
        })
      }
    }
}
</script>

<style scoped lang="scss">
  .active-color{
    color:#3C7FEE
  }
  .logistics-info{
    background: #E6E9ED;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .head{
    height:1.81rem;
    width: 100%;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(127,173,249,1));
    margin-bottom: .93rem;
    .progress{
      padding: .38rem .34rem 0 .34rem;
      &>div{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
      }
      .pro-con{
        padding:0 .28rem;
        align-items: center;
        height: .08rem;
        width: 100%;
        margin:.1rem 0 0 0;
        .point{
          width: .04rem;
          height: .04rem;
          border-radius: 50%;
          background: #fff;
        }
        .active-point{
          width: .08rem;
          height:.08rem;
        }
        .line{
          flex:1;
          height: .01rem;
          border:0;
          border-bottom: .01rem dotted #fff;
        }
      }
      .pro-content{
        height: .33rem;
        width: 100%;
        color: #fff;
        font-size: .15rem;
        &>div{
          flex:1;
          &:nth-child(2){
            text-align: center
          }
          &:nth-child(3){
            text-align: center
          }
          &:nth-child(4){
            text-align: right;
          }
        }
        .active-content{
          font-size: .17rem
        }
      }
      .add-con{
        font-size: .13rem;
        height:.25rem;
         &>div{
          flex:1;
          &:nth-child(2){
            text-align: right
          }
        }
      }
    } 
    .company-info{
      width: 3.5rem;
      height: 1.15rem;
      background: #fff;
      border-radius: .04rem;
      padding:0 .13rem;
      margin: 0 auto;
      display: flex;
      margin-top: .31rem;
      align-items: center;
      justify-content: space-between;
      img{
        width:.73rem;
        height: .73rem;
        margin-right: .2rem;
      }
      .now-add-con{
        flex: 1;
        overflow: hidden;
        p{
          font-size: .17rem;
          color: #333;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          span{
            color: #9A9A9A;
            font-size: .15rem;
          }
        }
      }
      .icon-yousanjiao1{
        color: #D7D7D7
      }
    }
  }
  .order-item{
    width:100%;
    height: 1.96rem;
    background: #fff;
    margin-bottom: .17rem;
    padding: 0 .04rem 0 .04rem;
    border-radius: .04rem;
    display: flex;
    flex-direction: column;
    position: relative;
    .order-info{
      flex:1;
      flex-direction: row;
      display: flex;
      padding: .21rem .09rem 0;
      position: relative;
      &>img{
        width:.73rem;
        height: .73rem;
        margin-right: .13rem;
      }
      .order-detail{
        flex:1;
        overflow: hidden;
        height: 100%;
        padding: 0;
        .order-title{
          text-overflow: ellipsis;
          white-space: nowrap; 
          overflow: hidden;
          width: 100%;
        }
        .order-title{
          color:#333333;
          font-size: .17rem;
        }
        &>div{
          color:#9A9A9A;
          font-size: .13rem;
          margin: .04rem 0;
        }
      }
    }
  }
  .logistics-con{
    width: 3.5rem;
    border-radius: .04rem;
    background: #fff;
    padding: .13rem;
    margin:0 auto;
    .tit{
      color: #333;
      font-size: .19rem;
      margin-bottom: .2rem;
    }
    .logistics-item{
      &:nth-last-child(1){
        .item-line{
          display: none;
        }
      }
      display: flex;
      justify-content: space-between;
      .dotted-line{
        width: .13rem;
        margin-right:.17rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        .icon-chenggong{
          color:#FFB127
        }
        .dotted{
          width:.06rem;
          height:.06rem;
          border-radius: 50%;
          background:#9A9A9A; 
        }
        .item-line{
          flex: 1;
          width:.01rem;
          border-left: .01rem dotted #C9C9C9;
        }
      }
      .info{
        flex: 1;
        padding-bottom:.2rem;
        color:#9A9A9A;
        div:nth-of-type(1){
          font-size: .13rem;
          span:nth-of-type(1){
            margin-right: .12rem;
          }
        }
        div:nth-of-type(2){
          font-size: .15rem;
        }
      }
    }
    .active-logistics{

    }
  }
</style>



